 /* Base Styles */
body {
    font-family: 'Arial', sans-serif;
    background-color: #000036; /* Dark space background */
    color: #e0e0e0;
    margin: 0;
    padding: 0;
}

/* Center the heading on the project index page */
h1 {
    text-align: center; /* Centers the text horizontally */
    margin-bottom: 30px; /* Adds some space below the heading */
}

/* Container */
.container {
    width: 80%;
    margin: auto;
    padding: 20px;
}

/* Header */
.header {
    text-align: center;
    padding: 20px 0;
    /* border-bottom: 2px solid #ffffff; /* Green border */ 
}

.header h1 {
    color: #ffffff; /* Green heading */
    font-size: 3em;
    margin-bottom: 10px;
}

.header p {
    font-style: italic;
    font-size: 1.5em;
    color: #b5b5b5;
}

/* Content */
.content {
    margin-top: 30px;
}

.profile-section, .cv-section {
    margin-bottom: 30px;
    background-color: #5c4be1;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.profile-section h2, .cv-section h2 {
    color: #ffffff; /* Green subheadings */
    font-size: 2em;
    border-bottom: 2px solid #ffffff;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

.profile-section p, .cv-section p {
    font-size: 1.1em;
    line-height: 1.8em;
    color: #d3d3d3;
}

/* Footer */
.footer {
    text-align: center;
    padding: 20px 0;
    margin-top: 50px;
    border-top: 2px solid #ffffff;
    color: #b5b5b5;
}

.footer p {
    margin: 0;
}

.current {
    color: green;
 }










 /* Navigation Bar */
.navbar {
    background-color: #16213e; /* Dark background to match the space theme */
    padding: 15px;
    text-align: center;
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Soft shadow for a lifted effect */
    margin-bottom: 30px;
}

.navbar .nav-link {
    display: inline-block;
    padding: 10px 20px;
    margin: 0 10px;
    background-color: #1a1a2e; /* Slightly lighter background for buttons */
    color: #e0e0e0;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease, color 0.3s ease; /* Smooth transitions */
}

.navbar .nav-link:hover {
    background-color: #28a745; /* Green background on hover */
    color: #ffffff; /* White text on hover */
}

.navbar .nav-link.current {
    background-color: #28a745; /* Active link with green background */
    color: #ffffff;
}












/* Post Layout */
.post {
    max-width: 800px;
    margin: 40px auto;
    padding: 20px;
    background-color: #1a1a2e; /* Dark background */
    color: #e0e0e0; /* Light text color */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Soft shadow */
}

.post-header h1 {
    color: #28a745; /* Green title color */
    font-size: 2.5em;
    margin-bottom: 10px;
}

.post-meta {
    font-size: 0.9em;
    color: #b5b5b5; /* Light gray for metadata */
    margin-bottom: 30px;
}

.post-content {
    line-height: 1.8em;
    font-size: 1.1em;
}

.post-content img {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 20px auto; /* Center images */
    border-radius: 10px; /* Rounded corners on images */
}

.post-content pre, .post-content code {
    background-color: #16213e; /* Slightly darker background for code blocks */
    color: #e0e0e0;
    padding: 10px;
    border-radius: 5px;
    font-size: 0.95em;
    line-height: 1.6em;
    overflow-x: auto;
}

.post-content pre {
    margin-bottom: 30px; /* Space after code blocks */
    box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.5);
}

.post-footer {
    text-align: center;
    margin-top: 40px;
    font-size: 0.9em;
    color: #b5b5b5; /* Light gray for footer text */
}

.post-footer p {
    margin: 0;
}




















/* Project Layout */
/* Project Layout */
.project {
    max-width: 800px;
    margin: 40px auto; /* Center the container and add vertical margin */
    padding: 20px; /* Add padding inside the container */
    background-color: #1a1a2e; /* Dark background */
    color: #e0e0e0; /* Light text color */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Soft shadow */
}


.project-header h1 {
    color: #28a745; /* Green title color */
    font-size: 2.5em;
    margin-bottom: 10px;
}

.project-affiliation {
    font-size: 1.1em;
    color: #b5b5b5; /* Light gray for affiliation */
    margin-bottom: 20px;
}

.project-content p {
    font-size: 1.2em;
    line-height: 1.8em;
    margin-bottom: 30px;
}

.project-media {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    margin-top: 20px;
}

.media-item img, .media-item video {
    width: 100%;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Slight shadow */
}

.project-footer {
    margin-top: 40px;
    text-align: center;
}

.back-link {
    text-decoration: none;
    color: #28a745;
    font-weight: bold;
}






/* Projects Index Page */
.projects-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Responsive grid layout */
    gap: 20px; /* Space between grid items */
    margin: 20px; /* Adds space around the entire grid */
    padding: 20px; /* Adds padding inside the grid container */
}

.project-summary {
    padding: 20px; /* Adds padding inside each project summary */
    background-color: #1a1a2e; /* Background color for each project summary */
    color: #e0e0e0; /* Text color */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Subtle shadow around each summary */
    margin: 10px 0; /* Adds vertical margin between project summaries */
}

.project-summary h2 a {
    color: #28a745; /* Color for the project title links */
    text-decoration: none; /* Remove underline from links */
}

.project-summary p {
    margin-bottom: 10px; /* Adds space below paragraphs inside project summaries */
}








/* Project Status Indicator */
.project-status {
    display: flex;
    align-items: center;
    margin-top: 10px;
}

.status-light {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 10px;
    display: inline-block;
}

.status-light.finished {
    background-color: #28a745; /* Green for finished */
}

.status-light.ongoing {
    background-color: #ffc107; /* Yellow for ongoing */
}

.status-text.finished {
    font-weight: bold;
    color: #28a745;
}

.status-text.ongoing {
    font-weight: bold;
    color: #ffc107;
}
